.bike {
  &__photo--container {
    position: relative;

    box-sizing: border-box;
    width: 100%;
    max-height: 320px;
    overflow: hidden;

    transition: transform 0.5s;

    &:hover {
      transform: scale(105%);
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;

      transition: transform 0.5s;
    }

    @include onTablet {
      height: 188px;
      padding-bottom: 0;
    }

    @include onDesktop {
      height: 320px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
  }

  &__photos {
    margin-bottom: 16px;
  }

  &__link {
    display: block;

    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: $text-color;
    text-decoration: none;

    margin-bottom: 8px;
  }

  &__text {
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;

    @include onTablet {
      margin-bottom: 48px;
    }
  }
}
