.icon {
  position: relative;

  display: block;
  width: 100%;
  padding-bottom: 100%;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;

  &--phone {
    background-image: url(../images/phone.svg);

    @include onTablet {
      width: 24px;
    }

    @include onDesktop {
      width: 24px;
    }
  }

  &--menu {
    background-image: url(../images/menu.svg);
    transition: background-image 0.3s;

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }

  &--cross {
    background-image: url(../images/cross.svg);

    left: 38px;

    @include onTablet {
      left: 53px;
    }

    @include onDesktop {
      left: 63px;
    }
  }

  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);

    display: block;
    width: max-content;
    padding-right: 20px;

    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    color: #fff;
    letter-spacing: 2px;
    text-transform: uppercase;
    opacity: 0;
    pointer-events: none;

    transition: opacity 0.5s;

  }

  &:hover &__tooltip {
    opacity: 1;
    pointer-events: all;
  }
}
